* {
  margin: 0;
  padding: 0;
}

.posters {
  background-color: #1d1d1d;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 0;
  line-height: 0;
  padding-bottom: 30px;

  &__item {
    flex-basis: 33%;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
    margin-top: 30px;
    overflow: hidden;

    picture,
    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    &-l {
      flex-basis: 66%;
    }//.posters__item-l
  }
}

.poster {
  position: relative;

  &__header {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;
    font-family: 'officinaserifmediumcregular', sans-serif;
    font-size: 28px;
    line-height: 1;
  }//.poster__header

  &__title {
    position: relative;
    display: inline-block;
    width: 68%;
    bottom: -18px;
    background: rgb(225,176,11); /* Old browsers */
    background: -moz-linear-gradient(left, rgba(225,176,11,1) 0%, rgba(239,201,56,1) 53%, rgba(252,244,71,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, rgba(225,176,11,1) 0%,rgba(239,201,56,1) 53%,rgba(252,244,71,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, rgba(225,176,11,1) 0%,rgba(239,201,56,1) 53%,rgba(252,244,71,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e1b00b', endColorstr='#fcf447',GradientType=1 ); /* IE6-9 */
    padding: 20px 30px 12px;
    font-size: 34px;
    font-weight: 500;
    color: #070709;
    letter-spacing: 0.025em;
  }//.poster__title

  &__info {
    display: block;
    background-color: rgba(0,0,0, .7);
    padding: 27px 32px 12px;
    color: #fff;
    letter-spacing: 0.035em;
  }//.poster__info
}
@media screen and (max-width: 1750px){
  .poster__header {
    font-size: 16px;
  }
  .poster__title {
    font-size: 24px;
    padding: 15px 15px 10px;
    bottom: -15px;
  }
  .poster__info {
    padding: 25px 15px 12px;
  }
}
@media screen and (max-width: 1240px){
  .poster__header {
    font-size: 14px;
  }
  .poster__title {
    font-size: 20px;
    padding: 12px 10px 8px;
    bottom: -12px;
    width: 80%;
  }
  .poster__info {
    padding: 22px 10px 10px;
  }
}
@media screen and (max-width: 960px){
  .poster__header {
    font-size: 12px;
  }
  .poster__title {
    font-size: 16px;
    padding: 10px 10px 5px;
  }
  .poster__info {
    padding: 18px 10px 8px;
  }
}
@media screen and (max-width: 767px){
  .posters {
    justify-content: flex-start;
    padding-bottom: 10px;
  }
  .posters__item {
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 10px;
    flex-basis: 50%;

    &-l {
      flex-basis: 100%;
    }//.posters__item-l

    &-mobile-xl {
      flex-basis: 100%;
    }//.posters__item-mobile-xl
  }
}
@media screen and (max-width: 560px){
  .poster__title {
    width: 100%;
    font-size: 12px;
    padding-left: 5px;
    padding-right: 5px;
  }
  .poster__info {
    font-size: 10px;
    padding-left: 5px;
    padding-right: 5px;
  }
}